

const Note = props => {
    return (
      <div
        onClick={event => {
            // console.log(event.target)
            props.noteObject.isCompleted = !props.noteObject.isCompleted
            props.noteObject.isCompleted ? event.target.className = "done" : ""
        }}
      >
        <span className={props.noteObject.isCompleted ? "done" : ""}>
            {props.noteObject.note}
        </span>
        <br />
        {props.noteObject.isCompleted}
      </div>
    );
}

export default Note

